import React, { Component } from "react";
import Carousel from "react-bootstrap/Carousel";
import "./CarouselSlider.css";

export default class CarouselSlider extends Component {

  render() {
    return (
      <Carousel
        controls={false}
        indicators={false}
        interval={5000}
        keyboard={false}
      >
        <Carousel.Item>
          <div className="slider-image-back im1">
            <div className="header-wrapper text-color">
              <h4>For all your zooshoping needs!</h4>
              <h1>Eden</h1>
              <a
                type="button"
                href="/shop"
                class="btn btn-primary rounded-pill btn-lg"
              >
                Let's Go shopping!
              </a>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="slider-image-back im2">
            <div className="header-wrapper text-color">
              <h4>For all your zooshoping needs!</h4>
              <h1>Eden</h1>
              <a
                type="button"
                href="/shop"
                class="btn btn-primary rounded-pill btn-lg"
              >
                Let's Go shopping!
              </a>
            </div>
          </div>
        </Carousel.Item>
        <Carousel.Item>
          <div className="slider-image-back im3">
            <div className="header-wrapper text-color">
              <h4>For all your zooshoping needs!</h4>
              <h1>Eden</h1>
              <a
                type="button"
                href="/shop"
                class="btn btn-primary rounded-pill btn-lg"
              >
                Let's Go shopping!
              </a>
            </div>
          </div>
        </Carousel.Item>
      </Carousel>
    );
  }
}

