import React, { Component } from "react";
import "./Home.css";
import CarouselSlider from "./CarouselSlider";
import SweetAlert from 'sweetalert2-react';

export class Home extends Component {
  render() {
    return (
      <div className="homepage-wrapper">
        <section>
          <div className="homepage">
            <CarouselSlider />
          </div>
        </section>
        <SweetAlert show="true" title="Message" text="This site is for demonstration purposes only, showcasing author's
            skills. This site isn't real, you can't purchase animal store products from here." onConfirm={this.hideAlert} />
      </div>
    );
  }
}
