import React from "react";
import "./Contacts.css";

export const Contacts = () => (
  <div className="contact-page-wrapper">
    <div className="contact-page">
      <div className="hero-image-contacts">
          <div className="jumbotron-contact d-none d-lg-block hero-image-desktop-version-contact">
              <div className="header-title-about">
                <h1 style={{ paddingTop: "3rem"}}>Contact Us</h1>
              </div>
              </div>
            <div className="jumbotron-contact d-block d-lg-none hero-image-mobile-version-contact">
              <div className="header-title-about">
                <h1 style={{ paddingTop: "5rem"}}>Contact Us</h1>
              </div>
            </div>
      </div>
      <div className="container-fluid">
        <div className="row">
          <div className="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-6 col-color">
            <div className="row styled-row">
              <div className="col-12 col-sm-12 col-md-12 col-lg-3 col-xl-3"></div>
              <div className="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-6">
              <div className="form-wrapper-new d-none d-lg-block">
              <div className="contact-form-new">
                <form>
                  <div class="form-group">
                    <label for="exampleInputEmail1">
                      Feel free to contact us!
                    </label>
                    <input
                      type="email"
                      class="form-control"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      placeholder="Your First Name"
                    />
                  </div>
                  <div class="form-group">
                    <input
                      type="email"
                      class="form-control"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      placeholder="Your Second Name"
                    />
                  </div>
                  <div class="form-group">
                    <input
                      type="email"
                      class="form-control"
                      id="exampleInputEmail1"
                      aria-describedby="emailHelp"
                      placeholder="Enter email"
                    />
                    <small id="emailHelp" class="form-text text-muted">
                      We'll never share your email with anyone else.
                    </small>
                  </div>
                  <div class="form-group">
                    <label for="exampleFormControlTextarea1">
                      Your Message
                    </label>
                    <textarea
                      class="form-control"
                      id="exampleFormControlTextarea1"
                      rows="3"
                    ></textarea>
                  </div>
                  <button type="submit" class="btn btn-primary">
                    Submit
                  </button>
                </form>
              </div>
            </div>
              </div>
              <div className="col-12 col-sm-12 col-md-12 col-lg-3 col-xl-3"></div>
            </div>
          </div>
          <div className="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-6">
            <div className="row styled-row">
              <div className="col-12 col-sm-12 col-md-12 col-lg-3 col-xl-3"></div>
              <div className="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-6">
                <div className="contact-info-stats">
                  <h4>Phone</h4>
                  <h6>8-800-900-90-90</h6>
                  <h4>E-mail</h4>
                  <h6>examplemail@gmail.com</h6>
                  <h4>Address</h4>
                  <h6>4706 Broadway Blvd, Kansas City, MO 64112, US</h6>
                </div>
              </div>
              <div className="col-12 col-sm-12 col-md-12 col-lg-3 col-xl-3"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
);
