import React from "react";
import "./App.css";
import "./Main.css";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
//import { Router, Route, useRouterHistory, Switch } from 'react-router';
//import { createBrowserHistory } from "history";
import { createBrowserHistory } from "history";
import { useRouterHistory } from 'react-router';
import { Home } from "./Home";
import { About } from "./About";
import { Shop } from "./Shop";
import { Contacts } from "./Contacts";
import { Foot } from "./components/Footer";
import { NavigationBar1 } from "./components/Nav_bar";

function App() {
  {/* 
  const history = useRouterHistory(createBrowserHistory)({
    basename: '/dist'
  });
  */}
  
  const history = createBrowserHistory({ basename: 'https://github.com/yuryjin/reactjs-eden/' });

  return (
    <div className="App">
      <React.Fragment>
        <NavigationBar1 />
        <div className="parent">
          <Router basename="https://github.com/yuryjin/reactjs-eden/">
            <Switch>
              <Route exact path="/" component={Home}></Route>
              <Route path="/about" component={About}></Route>
              <Route path="/shop" component={Shop}></Route>
              <Route path="/contacts" component={Contacts}></Route>
            </Switch>
          </Router>
        </div>
        <Foot />
      </React.Fragment>
    </div>
  );
}

export default App;
